#pointsSection{
    padding: 40px 0;
    background: $white;
}
#pointsSection .container{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}
.pointSingle{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    flex-basis: 0;
    padding: 10px;
}
.pointSingle__imgWrap{
    border-radius: 50%;
    height: 90px;
    width: 90px;
    padding: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $green-100;
}
.pointSingle__imgWrap img{
    width: 100%;
}
