.pointSingle__title{
    padding-top: 10px;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    color: $gray-800;
}

@media screen and (max-width: 991px){
    #pointsSection {
        padding: 20px 0;
    }
}
@media screen and (max-width: 500px){
    .pointSingle{
        width: 33.333%;
        flex-basis: 33.333%;
    }
    .pointSingle__imgWrap {
        border-radius: 50%;
        height: 60px;
        width: 60px;
    }
    .pointSingle__imgWrap img{
        width: 50px;
    }
}