.category-title{
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
    font-size: 30px;
    font-weight: 600;
    padding-top: 20px;
    margin: 0px 0 40px 0;
    padding-bottom: 15px;
    color: $gray-800;

    &::after{
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        height: 6px;
        width: 40px;
        background: $green-200;
    }
    
    & + h4{
        display: none;
    }
}
.category-perex{
    font-size: 14px;
    margin-top: -20px;
    margin-bottom: 40px;
}
.category-header{
    display: flex;
    flex-direction: column;
    margin: 0;
    align-items: flex-end;
    margin: 0px 0;
}
.category-header div{
    font-size: 14px;
    color: $gray-700;
}
.category-header label{
    cursor: pointer;
    margin-right: 20px;
}
.category-header label:last-child{
    margin-right: 0px;
}
.category-header input{
    -webkit-appearance: none;
    display: none;
}

.category-header input:checked + label{
    color: $green-700;
}

@media screen and (max-width: 991px){
    .category-title{
        font-size: 24px;
        margin: 20px 0 20px 0;
    }
    .category-header{
        display: none;
    }  
    .filter-sections .category-header{
        display: flex;
        border-top: 1px solid $gray-400;
    }  
    .category-header form{
        width: 100%;
    }  
    .category-header fieldset{
        display: flex;
        justify-content: flex-start;
        padding: 0;
    }  
    .category-header div{
        display: none;
    }
    .category-header label{
        margin: 0px;
        height: 46px;
        display: flex;
        align-items: center;
        padding: 0 10px;
    }
}